Покупателем в течение 15 (пятнадцати) календарных дней с момента получения доступа к обучающему продукту. Порядок возврата урегулирован в Пользовательском соглашении. Также Продавец гарантирует, что ему принадлежит право на коммерческое использование обучающего продукта и всех (любых) его составных частей, достаточное для размещения и продажи обучающего продукта на настоящем сайте. Покупателю не могут быть предъявлены претензии со стороны третьих лиц в связи с незаконным использованием обучающего продукта. По всем вопросам к Продавцу Покупатель может обратить по телефону (916) 191-64-95 и адресу электронной почты letterlandrussia@mail.ru. Обращения Покупателей обрабатываются в рабочие дни с 9.00 до 18.00 по местному времени Продавца.\r\n            ")]),_c('el-button',{staticClass:"hidden-sm-and-up",attrs:{"type":"primary","size":"mini","plain":""},domProps:{"textContent":_vm._s(_vm.textVision ? 'Скрыть' : 'Читать полностью')},on:{"click":function($event){_vm.textVision = !_vm.textVision}}})],1)],1),_c('el-footer',{staticClass:"footer",attrs:{"height":"auto"}},[_c('div',{staticClass:"text-white footer-content"},[_vm._v("\r\n      Размещено на Learme. © Learme 2019, "),_c('a',{staticClass:"text-white",attrs:{"href":""}},[_vm._v("Пользовательское соглашение")])])])],1)])}
